/* 
importing style
*/
import "./style.css";

/* 
optimized imports from threejs
*/
import {
  Vector2,
  SphereGeometry,
  MeshPhongMaterial,
  Mesh,
  VideoTexture,
  PlaneBufferGeometry,
  MeshLambertMaterial,
  DoubleSide,
} from "three";

/* 
imports of our refactored modules for setting up the scene, adding the showroom
and adding the settings respectively
*/
import BasicScene from "./scene";
import InitShowRoom from "./init";
import Settings from "./settings";

/* 
imports of the global variables that holds all the important states of the whole system
for central management, HTMLInitializer is a module that sets up the HTML side of things 
*/
import GlobalVarsInit from "./vars";
import HTMLInitializer from "./page_init";
import AudioManager from "./audio_management/audio";
import AudioSource from "./audio_management/audio_source";
import GlobalLoaderManager from "./loading manager/loading_manager";

/* 
our main class that bootstraps everything =]
*/
export default class Sketch {
  constructor(options) {
    /* 
    below are a couple of varaibles that are gloabal across this class, we use these variables to
    track some things and adding things to settings etc. ^-^
    */
    this.container = options.dom;

    this.sizes = {
      width: window.innerWidth,
      height: window.innerHeight,
    };

    this.showSettings = true;

    this.debugObject = {
      rendererParams: {},
      controllerParams: {},
      charachterParams: {
        velocityDampeningAmount: new Vector2(10, 10),
        jumpAmount: 350,
        gravity: 9.8,
        mass: 100,
        moveAmount: new Vector2(2000, 2000),
        eyeLevel: 40,
      },
    };

    this.mouse = {
      x: 0,
      y: 0,
      prevX: 0,
      prevY: 0,
      vX: 0,
      vY: 0,
    };

    /* 
    an array that holds objects that are used to do our raycasting for jumping :)

    Note : please try to look for object.push(...) inside init.js, those objects are the
           ones we want to track for our raycasting and jumping :}
    */
    this.objects = [];

    /* 
    setting up the HTML side of things and setting up the global variable bridge
    between the HTML and threejs systems :)

    please read comments and explanations in the order of initialization and calls for a clear
    picture
    */
    this.G = new GlobalVarsInit();
    new HTMLInitializer(this.G);

    /* 
    setting up the threejs part of things :/
    */
    this.setup();
  }

  async setup() {
    /* 
    please check out the respective sources for explanations of what this modules do, 
    we basically instantiate a new object i.e call whatever code is setup in the constructor
    of these classes :)

    please read in the order they are initialized to get a clearer picture, 

    */
    new GlobalLoaderManager(this);

    new BasicScene(this);

    new InitShowRoom(this);

    new Settings(this);

    this.testVideoTexture();

    this.render();
  }

  testVideoTexture() {
    this.video = document.getElementById("video");
    this.video.load();
    // this.video.play();

    console.log(this.video);

    this.vidTexture = new VideoTexture(this.video);

    this.videoPlaneGeo = new PlaneBufferGeometry(10 * 16, 10 * 9, 128, 128);
    this.videoPlaneMat = new MeshLambertMaterial({
      color: 0xffffff,
      map: this.vidTexture,
      side: DoubleSide,
    });

    this.videoPanel = new Mesh(this.videoPlaneGeo, this.videoPlaneMat);
    this.videoPanel.rotateX(90 * Math.PI);
    this.videoPanel.rotateY(60 * Math.PI);
    this.videoPanel.position.y = 10 * 9 * 0.5;
    this.videoPanel.position.z = -180;
    this.videoPanel.position.x = -180;

    this.scene.add(this.videoPanel);

    window.addEventListener("keypress", (e) => {
      if (e.code == "KeyV") {
        this.video.play();
      } else if (e.code == "KeyZ") {
        this.video.pause();
      }
    });
  }

  /* 
  this is the same old render function, it basically do updates of things that need to be
  updated in a frame by frame basis
  */
  render() {
    // return;
    this.time = this.clock.getDelta();
    this.stat.update();

    this.playerController.update();

    this.renderer.render(this.scene, this.camera);

    window.requestAnimationFrame(this.render.bind(this));

    this.stat.end();
  }
}

/* 
here we just create a new object, give it an argument of an object that contains some params, 
just by creating a new object our constructor is called automatically and everything bootstraps 
from then on :]
*/
const canvas = new Sketch({
  dom: document.getElementById("webgl"),
});
