import * as dat from "dat.gui";
/* 
aside from being extracted into a separate module this is the same old dat.gui settings
let me know if anything is not clear
*/
export default class Settings {
  constructor(context) {
    context.settings = {
      sampleControl: 0,
    };

    if (!context.showSettings) return;
    context.gui = new dat.GUI();

    context.lightProperties = context.gui.addFolder("Light Properties");
    context.lightProperties.add(context.ambLight, "intensity", 0, 2, 0.01);
    context.lightProperties.open();

    context.rendererProperties = context.gui.addFolder("Renderer Properties");

    context.cameraProperties = context.gui.addFolder("Camera Properties");

    context.controllerProperties = context.gui.addFolder(
      "Controller Properties "
    );

    context.characterController = context.gui.addFolder(
      "Charachter Properties"
    );
    context.characterController
      .add(
        context.debugObject.charachterParams.velocityDampeningAmount,
        "x",
        0,
        40,
        0.25
      )
      .name("X Velocity Damp");
    context.characterController
      .add(
        context.debugObject.charachterParams.velocityDampeningAmount,
        "y",
        0,
        40,
        0.25
      )
      .name("Z Velocity Damp");
    context.characterController
      .add(context.debugObject.charachterParams, "gravity", 1, 20, 0.25)
      .name("Gravity");
    context.characterController
      .add(context.debugObject.charachterParams, "mass", 40, 200, 0.25)
      .name("Mass");
    context.characterController
      .add(context.debugObject.charachterParams, "jumpAmount", 300, 1200, 0.25)
      .name("Jump Amount");
    context.characterController
      .add(context.debugObject.charachterParams.moveAmount, "x", 50, 3000, 5)
      .name("X Move Amount");
    context.characterController
      .add(context.debugObject.charachterParams.moveAmount, "y", 50, 3000, 5)
      .name("Z Move Amount");

    context.characterController
      .add(context.debugObject.charachterParams, "eyeLevel", 5, 100, 0.25)
      .name("Eye Elevation");

    context.characterController.open();
  }
}
