import { Vector3, Raycaster } from "three";
import { PointerLockControls } from "three/examples/jsm/controls/PointerLockControls.js";

/* 
welcome, this module holds a class that handles all the logic needed for
 charachter movement and navigation
*/
export default class CharacterController {
  constructor(context) {
    this.context = context;

    /* 
    we setup some varaibale to hold the velocity of our charachter 
    and the direction its heading
    */
    this.context.velocity = new Vector3();
    this.context.direction = new Vector3();

    /* 
    we create some additional booleans to help us modulate a smooth navigation
    
    for example we want to make sure the charachter is not moving on the right
    direction and on the left direction at the same time, it'd get stuck & we dont want
    the charachter to be jumping off of air, we make sure that by checking + setting theses
    boolean values accordingly based on the state of movement of the charachter :), 

    (I hope I'm not boring you with me nerding about all this stuff)
    */
    this.context.moveForward = false;
    this.context.moveBackward = false;
    this.context.moveLeft = false;
    this.context.moveRight = false;
    this.context.canJump = false;

    /* 
    these are just the html elements of the blocker/overlay and the instruction manual, 
    we want to hide and show them based some events like click on the screen and when "ESC" key
    is pressed
    */

    let blocker = document.getElementById("blocker");
    let instructions = document.getElementById("instructions");

    /* 
    creating the actual controller
    */
    this.context.controls = new PointerLockControls(
      this.context.camera,
      this.context.renderer.domElement
    );

    /* 
    add a click listner to the overlay element, why the overlay elemet?
    => because we want the overlay to listen to clicks because we dont want to be
    annoyed while adjusting settings or while clicking other buttons in the future :/
    */
    blocker.addEventListener(
      "click",
      () => {
        this.context.controls.lock();
      },
      false
    );

    /* 
    hide the ovrelay and instruction screen when our cursor is locked,
    */
    this.context.controls.addEventListener("lock", function () {
      instructions.style.display = "none";
      blocker.style.display = "none";
    });

    /* 
    show the ovrelay and instruction screen when our cursor is locked,
    */
    this.context.controls.addEventListener("unlock", function () {
      blocker.style.display = "block";
      instructions.style.display = "";
    });

    /* 
    event handler function for our keyboard events, 
    we basically set those booleans to let our update loop know
    what is possible and whats not possible
    */
    let onKeyDown = (event) => {
      switch (event.code) {
        case "ArrowUp":
        case "KeyW":
          this.context.moveForward = true;
          break;

        case "ArrowLeft":
        case "KeyA":
          this.context.moveLeft = true;
          break;

        case "ArrowDown":
        case "KeyS":
          this.context.moveBackward = true;
          break;

        case "ArrowRight":
        case "KeyD":
          this.context.moveRight = true;
          break;

        case "Space":
          if (this.context.canJump === true)
            this.context.velocity.y +=
              this.context.debugObject.charachterParams.jumpAmount;
          this.context.canJump = false;
          break;
      }
    };

    let onKeyUp = (event) => {
      switch (event.code) {
        case "ArrowUp":
        case "KeyW":
          this.context.moveForward = false;
          break;

        case "ArrowLeft":
        case "KeyA":
          this.context.moveLeft = false;
          break;

        case "ArrowDown":
        case "KeyS":
          this.context.moveBackward = false;
          break;

        case "ArrowRight":
        case "KeyD":
          this.context.moveRight = false;
          break;
      }
    };

    document.addEventListener("keydown", onKeyDown);
    document.addEventListener("keyup", onKeyUp);

    this.context.raycaster = new Raycaster(
      new Vector3(),
      new Vector3(0, -1, 0),
      0,
      10
    );
  }

  update() {
    if (this.context.controls.isLocked === true) {
      this.context.raycaster.ray.origin.copy(
        this.context.controls.getObject().position
      );
      this.context.raycaster.ray.origin.y -= 10;

      let intersections = this.context.raycaster.intersectObjects(
        this.context.objects,
        false
      );

      let onObject = intersections.length > 0;

      let delta = this.context.time;

      this.context.velocity.x -=
        this.context.velocity.x *
        this.context.debugObject.charachterParams.velocityDampeningAmount.x *
        delta;
      this.context.velocity.z -=
        this.context.velocity.z *
        this.context.debugObject.charachterParams.velocityDampeningAmount.y *
        delta;
      this.context.velocity.y -=
        this.context.debugObject.charachterParams.gravity *
        this.context.debugObject.charachterParams.mass *
        delta; // 100.0 = mass

      this.context.direction.z =
        Number(this.context.moveForward) - Number(this.context.moveBackward);
      this.context.direction.x =
        Number(this.context.moveRight) - Number(this.context.moveLeft);
      this.context.direction.normalize(); // this.context ensures consistent movements in all directions

      if (this.context.moveForward || this.context.moveBackward)
        this.context.velocity.z -=
          this.context.direction.z *
          this.context.debugObject.charachterParams.moveAmount.x *
          delta;
      if (this.context.moveLeft || this.context.moveRight)
        this.context.velocity.x -=
          this.context.direction.x *
          this.context.debugObject.charachterParams.moveAmount.y *
          delta;

      if (onObject === true) {
        this.context.velocity.y = Math.max(0, this.context.velocity.y);
        this.context.canJump = true;
      }

      this.context.controls.moveRight(-this.context.velocity.x * delta);
      this.context.controls.moveForward(-this.context.velocity.z * delta);

      this.context.controls.getObject().position.y +=
        this.context.velocity.y * delta; // new behavior

      if (
        this.context.controls.getObject().position.y <
        this.context.debugObject.charachterParams.eyeLevel
      ) {
        this.context.velocity.y = 0;
        this.context.controls.getObject().position.y =
          this.context.debugObject.charachterParams.eyeLevel;

        this.context.canJump = true;
      }
    }
  }
}
