import {
  sRGBEncoding,
  CubeTextureLoader,
  MeshBasicMaterial,
  MeshStandardMaterial,
  PlaneBufferGeometry,
  Mesh,
  MeshNormalMaterial,
} from "three";

/* 
this module contains a class definition of our custom audio object 
*/
import AudioSource from "./audio_management/audio_source";

/* 
these modules are more of a utility we use to load models and texxtures smoothly
we'll be adding more and more utilities as we go on
*/
import TexLoader from "./loaders/textureLoader";
import GLTFmodelLoader from "./loaders/GLTFmodelLoader";

export default class InitShowRoom {
  constructor(context) {
    this.context = context;

    this.gltfModelLoader = new GLTFmodelLoader(this.context);
    this.textureLoader = new TexLoader(this.context);

    this.init();
  }

  async loadTextures() {
    this.context.floorTexture = await this.textureLoader.loadTexture(
      "/textures/matcaps/5.png"
    );

    this.context.domeTexture = await this.textureLoader.loadTexture(
      "/textures/bricks/normal.jpg"
    );
  }

  async loadEnvMap() {
    /* 
    todo : please replace the envmap loader with hdri loader or something like that
    */
    this.cubeTextureLoader = new CubeTextureLoader();
    this.context.environmentMap = this.cubeTextureLoader.load([
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
      "/textures/matcaps/5.png",
    ]);

    this.context.environmentMap.encoding = sRGBEncoding;
    //uncomment the code below after loading an hdri envmap
    /* this.context.scene.background = this.context.environmentMap;
    this.context.scene.environment = this.context.environmentMap; */
  }

  async init() {
    this.loadEnvMap();
    this.loadTextures();

    /* 
    adding our environment
    */
    this.context.envt = await this.loadEnvt();
    this.context.scene.add(this.context.envt);
    this.context.objects.push(this.context.envt);

    /* 
    adding our logo
    */
    this.context.blueCircleLogo = await this.loadBlueCircleLogoModel();
    this.context.scene.add(this.context.blueCircleLogo);
    this.context.objects.push(this.context.blueCircleLogo);

    /* 
    adding audio to our logo mesh
    */
    this.logoAudio = new AudioSource({
      name: "Logo Audio",
      parentMesh: this.context.blueCircleLogo,
      audioFileUrl: this.context.G.AUDIO_FILE_URLS.logoAudioFile,
      context: this.context,
    });
    setTimeout(() => {
      this.context.audioManager.addAudioToScene(this.logoAudio);
    }, 5000);
  }

  async loadBasicGroundMesh() {
    this.context.basicMatty = new MeshBasicMaterial({
      color: new THREE.Color("#ccc"),
      wireframe: true,
    });
    this.context.groundGeo = new PlaneBufferGeometry(1000, 1000, 16, 16);
    this.context.ground = new Mesh(
      this.context.groundGeo,
      this.context.basicMatty
    );
    this.context.ground.rotation.set(-Math.PI / 2, 0, 0);
    this.context.scene.add(this.context.ground);
    this.context.objects.push(this.context.ground);
  }

  async loadBasicFloor() {
    let floorGeo = new PlaneBufferGeometry(2000, 2000, 64, 64);
    let floorMat = new MeshBasicMaterial({
      map: this.context.floorTexture,
      color: "#444",
      wireframe: true,
    });

    this.context.floor = new Mesh(floorGeo, floorMat);

    this.context.floor.rotation.x = -Math.PI * 0.5;
    this.context.floor.position.y = 0;
    this.context.scene.add(this.context.floor);
    this.context.objects.push(this.context.floor);
  }

  /* 
  loading the models normally, might traverse over child meshes to apply some
  material or to change some props
   */

  async loadEnvt() {
    let { model } = await this.gltfModelLoader.loadModel(
      "/new_envt/geometry.glb"
    );
    model.scale.set(40, 40, 40);

    let mat = new MeshNormalMaterial({});

    model.traverse((child) => {
      if (child.isMesh) {
        child.material = mat;
      }
    });
    return model;
  }

  async loadBlueCircleLogoModel() {
    let logoMaterial = new MeshStandardMaterial({
      color: 0x824848,
      roughness: 0.2,
      metalness: 0.8,
    });

    let { model } = await this.gltfModelLoader.loadModel(
      "/Models/blueCircleLogo.gltf"
    );

    model.traverse((child) => {
      if (child.isMesh) {
        child.material.map = true;
        child.material.opacity = 1;
        child.material = logoMaterial;
      }
    });

    model.scale.set(40, 40, 40);
    model.position.set(0, 0, 0);

    return model;
  }
}
